@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700;900&display=swap");

*,
::after,
::before {
  font-family: "Roboto", sans-serif;
  box-sizing: border-box;
}

body {
  margin: 0;
}

header {
  text-align: center;
  padding: 50px;
}

header h1 {
  margin: 0;
}

.task-contain {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 40px;
}

form {
  max-width: 800px;
  margin: 0 auto;
}

.grpme {
  display: flex;
  align-items: center;
  box-shadow: 0 4px 12px rgb(0 0 0 / 8%), 0 0 1px rgb(1 0 0 / 10%);
  background: #fff;
  border-radius: 5px;
  border: solid 8px #fff;
}

.btn-sub {
  width: 100px;
  font-size: 15px;
  background: #333;
  color: #fff;
  border: none;
  border-radius: 5px;
  padding: 10px;
  cursor: pointer;
  border: none;
}
.btn-sub:hover {
  background: #000;
}

.grpme input[type="text"] {
  width: calc(100% - 100px);
  padding: 10px;
  border: none !important;
  outline: none !important;
  font-size: 15px;
}

.listMe {
  max-width: 800px;
  margin: 0 auto;
  list-style: none !important;

  padding: 0;
  margin-top: 60px;
}

.listMe li {
  border: solid 1px #e0e0e0;
  border-radius: 5px;
  position: relative;
  padding: 10px;
  margin-bottom: 15px;
  background: #fff;
  transition: all 0.3s;
}

.listMe li span {
}
.listMe li button {
  position: absolute;
  top: 8px;
  right: 8px;
  background: transparent;
  border: none;
  font-size: 11px;
  cursor: pointer;
  background: rgba(0, 0, 0, 0.08);
  padding: 5px 10px;
  border-radius: 100px;
  color: #555;
  transition: all 0.3s;
}
.listMe li button:hover {
  background: rgba(0, 0, 0, 0.1);
}

.listMe li:hover {
  box-shadow: 0 4px 12px rgb(0 0 0 / 8%), 0 0 1px rgb(1 0 0 / 10%);
}
